import React, { Component } from 'react';
import {connect} from "react-redux";
import { createForm } from 'rc-form';
import * as actions from "../actions";
import * as actionType from '../actionType.js';

class Register extends Component {
    componentDidMount() {
    }

    submit = () => {
        this.props.form.validateFields((error, value) => {
            const errs = [];
            if(!error) {
                if(value.login.length < 6) {
                    errs.push("登录名长度至少六位")
                }
                if(value.email.indexOf("@")<2) {
                    errs.push("邮箱不合法")
                }
                if(value.password.length<6) {
                    errs.push("密码长度至少六位")
                }
                if(value.password.length !== value.password2.length) {
                    errs.push("两次输入的密码不一致")
                }
                if(errs.length > 0) {
                    this.props.updateState(errs)
                    return;
                }
                value.langKey="zh_cn"
                this.props.register(value)
            }
        });
    }
    render() {
        const { getFieldProps, getFieldError } = this.props.form;
        const {history} = this.props;
        const token = localStorage.getItem("token")
        const user = localStorage.getItem("user")
        if(token && user) {
            history.push("/")
        }
        return (
            <div className="register">
                {
                    this.props.successed ? "注册成功,请打开邮箱进行验证." :
                        <div>
                            <ul className="top-err">
                                {
                                    this.props.errs.map(err=>(<li>{err}</li>))
                                }
                            </ul>
                            登录名
                            <span className="err">
                                {getFieldError('login')?"必须录入登录名":"" }
                            </span>
                            <input {...getFieldProps('login', {
                                onChange(){}, // have to write original onChange here if you need
                                rules: [{required: true}],
                            })}/>
                            邮箱
                            <span className="err">
                                {getFieldError('email')?"必须录入邮箱":"" }
                            </span>
                            <input {...getFieldProps('email', {
                                onChange(){}, // have to write original onChange here if you need
                                rules: [{required: true}],
                            })}/>
                            密码
                            <span className="err">
                                {getFieldError('password')?"必须录入密码":"" }
                            </span>
                            <input type="password" {...getFieldProps('password', {
                                onChange(){}, // have to write original onChange here if you need
                                rules: [{required: true},{minLen:5}],
                            })}/>
                            重新输入密码
                            <span className="err">
                                {getFieldError('password2')?"必须录入密码":"" }
                            </span>
                            <input type="password" {...getFieldProps('password2', {
                                onChange(){}, // have to write original onChange here if you need
                                rules: [{required: true}],
                            })}/>

                            <button className="btn btn-success" onClick={this.submit}>确定</button>
                        </div>
                }


            </div>
        );
  }
}

const mapStateToProps = (state) => {
    return {
        errs:state.register.errs,
        successed:state.register.successed
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        register: (values) => {
            dispatch(actions.register(values));
        },
        updateState:(errs) => {
            dispatch({type:actionType.REGISTER_UPDATE_ERRS,errs:errs})
        }
    }
};

export default createForm()(connect(mapStateToProps,mapDispatchToProps)(Register));

