import shortid from "shortid";

export const find = (jsonArray, name, value) => {
    const result = [];
    for(let item in jsonArray) {
        if(item[name] === value) {
            result.push(item);
        }
    }
    return result;
}
export const clone = (json) => {
    return JSON.parse(JSON.stringify(json));
}

export const createMenuItem = () => ({id:shortid.generate(),name:"新建菜单",pub:false,open:true,edit:true,child:[]})
export const reNameMenu = (id,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            json[i]['edit'] = true
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            reNameMenu(id,json[i].child)
        }
    }
}
export const confirmMenu = (id,newValue,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            json[i]['name'] = newValue;
            json[i]['edit'] = false
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            confirmMenu(id,newValue, json[i].child)
        }
    }
}
export const addSiblingBefore = (id,json) => {
    return addSibling(id,0,json)
}
export const addSiblingAfter = (id,json) => {
    return addSibling(id,1,json)
}
export const addSibling = (id,pos,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            json.splice(Number(i)+Number(pos),0,createMenuItem())
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            addSibling(id,pos,json[i].child)
        }
    }
}
export const addChild = (id,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            const child = json[i].child || [];
            child.push(createMenuItem())
            json[i].child = child;
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            addChild(id,json[i].child)
        }
    }
}
export const changePub = (id,pub,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            json[i]['pub'] = pub;
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            changePub(id,pub,json[i].child)
        }
    }
}
export const delMenu = (id,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            if(json[i].child && json[i].child.length > 0) {
                alert("请先删除子菜单!")
            } else {
                json.splice(i,1)
            }
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            delMenu(id,json[i].child)
        }
    }
}
export const moveUp = (id,json) => {
    return changePos(id, -1, json)
}
export const moveDown = (id,json) => {
    return changePos(id, 1, json)
}
export const changePos = (id,pos,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            let tmp = json.splice(i,1)
            json.splice(Number(i)+Number(pos),0,tmp[0])
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            changePos(id,pos,json[i].child)
        }
    }
}
export const toggle = (id,json) => {
    for(let i in json) {
        if(json[i].id === id) {
            if(json[i].child && json[i].child.length > 0) {
                json[i].open = !json[i].open
            }
            return;
        }
        if(json[i].child && json[i].child.length > 0) {
            toggle(id,json[i].child)
        }
    }
}