import React, { Component } from 'react';
import {connect} from "react-redux";
import localForage from "localforage";
import * as jsonutil from '../../../common/jsonutil.js'
import Input from './input.js'
import * as actions from "../actions";

class Menu extends Component {
    state = {
        menu:[
            // {id:1,name:"xxxxxxx1",pub:false,open:true},
            // {id:2,name:"xxxxxxx2",pub:false,open:true,child:[
            //         {id:22,name:"xxxxxxx22",pub:true,open:true},
            //         {id:23,name:"xxxxxxx23",pub:false,open:true},
            //         {id:24,name:"xxxxxxx24",pub:false,open:false,child:[
            //                 {id:245,name:"xxxxxxx245",pub:true,open:true},
            //                 {id:246,name:"xxxxxxx246",pub:false,open:true}
            //             ]},
            //         {id:25,name:"xxxxxxx25",pub:false,open:true},
            //         {id:26,name:"xxxxxxx26",pub:false,open:true}
            //     ]},
            // {id:3,name:"xxxxxxx3",pub:true,open:true},
            // {id:4,name:"xxxxxxx4",pub:false,open:true},
            // {id:5,name:"xxxxxxx5",pub:false,open:true},
            // {id:6,name:"xxxxxxx6",pub:false,open:true}
        ],
        actionMenu:"",
        curr:246
    }
    componentDidMount() {
        localForage.getItem("menu").then((v)=>{
            let sto = v || {};
            this.setState({menu:sto.menu||[],curr:sto.curr,actionMenu:sto.actionMenu})
        })
    }

    showMenu = (menuId) => {
        this.setState({actionMenu:menuId})
    }
    hideMenu = () => {
        this.setState({actionMenu:""})
    }
    toggle = (item) => {
        const {history} = this.props;
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.toggle(item.id, menu)
        localForage.setItem("menu", {menu:this.state.menu,curr:item.id,actionMenu:this.state.actionMenu})
            .then(this.setState({curr:item.id},history.push("/note/"+item.id)))
    }

    saveLocal = (menu) => {
        localForage.setItem("menu", {menu:menu,curr:this.state.curr,actionMenu:this.state.actionMenu}).then(this.setState({menu:menu}))
    }
    addFirstMenu = () => {
        let menu = [jsonutil.createMenuItem()]
        this.saveLocal(menu)
    }
    reName = (id) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.reNameMenu(id,menu);
        this.saveLocal(menu)
    }
    confirmName = (id,newValue) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.confirmMenu(id,newValue,menu);
        this.saveLocal(menu)
    }
    addSiblingBefore = (id) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.addSiblingBefore(id, menu);
        this.saveLocal(menu)
    }
    addSiblingAfter = (id) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.addSiblingAfter(id, menu);
        this.saveLocal(menu)
    }
    addChild = (id) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.addChild(id, menu);
        this.saveLocal(menu)
    }
    changePub = (id,pub) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.changePub(id,pub, menu);
        this.saveLocal(menu)
    }
    delMenu = (id) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.delMenu(id, menu);
        // this.saveLocal(menu)
        localForage.setItem("menu", {menu:menu,curr:"",actionMenu:""}).then(this.setState({menu:menu,curr:"",actionMenu:""}))
    }
    moveUp = (id) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.moveUp(id, menu);
        this.saveLocal(menu)
    }
    moveDown = (id) => {
        let menu = jsonutil.clone(this.state.menu);
        jsonutil.moveDown(id, menu);
        this.saveLocal(menu)
    }
    getDD = (menus=[]) => {
        const result = [];
        for(let i=0; i<menus.length; i++) {
            if(menus[i].child && menus[i].child.length > 0) {
                result.push(
                    <dd className='has-child' key={menus[i].id}>
                        <i style={{float:"left",lineHeight:'37px',marginRight:"2px"}} className="fa fa-folder-open"></i>
                        <dl>
                            <dt className={this.state.curr === menus[i].id ? "active" : ""}
                                onClick={()=>this.toggle(menus[i])}>
                                {
                                    menus[i].edit ?
                                        <Input value={menus[i].name} submit={(value)=>{this.confirmName(menus[i].id,value)}} /> :
                                        menus[i].name
                                }
                                <i className={menus[i].pub ? "fa fa-unlock" : "fa fa-lock"}
                                   onMouseOver={()=>this.showMenu(menus[i].id)}
                                   onMouseOut={()=>this.hideMenu()}>
                                    <ul className={this.state.actionMenu === menus[i].id ? "show" : "hide"}>
                                        <li onClick={()=>this.reName(menus[i].id)}>重命名</li>
                                        {
                                            menus[i].pub ?
                                                <li onClick={()=>this.changePub(menus[i].id,false)}>私有</li> :
                                                <li onClick={()=>this.changePub(menus[i].id,true)}>公开</li>
                                        }
                                        <hr/>
                                        <li onClick={()=>this.delMenu(menus[i].id)}>删除</li>
                                        {
                                            i > 0 ? <li onClick={()=>this.moveUp(menus[i].id)}>上移</li> : ""
                                        }
                                        {
                                            i < menus.length-1 ? <li onClick={()=>this.moveDown(menus[i].id)}>下移</li> : ""
                                        }
                                        {/*<li>移动到...</li>*/}
                                        <hr/>
                                        <li onClick={()=>this.addChild(menus[i].id)}>添加子菜单</li>
                                        <li onClick={()=>this.addSiblingBefore(menus[i].id)}>上方添加同级菜单</li>
                                        <li onClick={()=>this.addSiblingAfter(menus[i].id)}>下方添加同级菜单</li>
                                    </ul>
                                </i>
                            </dt>
                            {menus[i].open ? this.getDD(menus[i].child) : ""}
                        </dl>
                    </dd>
                );
            } else {
                result.push(
                    <dd key={menus[i].id}
                        className={this.state.curr === menus[i].id ? "active" : ""}
                        onClick={()=>this.toggle(menus[i])}>
                        {
                            menus[i].edit ?
                                <Input value={menus[i].name} submit={(value)=>{this.confirmName(menus[i].id,value)}} /> :
                                menus[i].name
                        }
                        <i className={menus[i].pub ? "fa fa-unlock" : "fa fa-lock"}
                           onMouseOver={()=>this.showMenu(menus[i].id)}
                           onMouseOut={()=>this.hideMenu()}>
                            <ul className={this.state.actionMenu === menus[i].id ? "show" : "hide"}>
                                <li onClick={()=>this.reName(menus[i].id)}>重命名</li>
                                {
                                    menus[i].pub ?
                                        <li onClick={()=>this.changePub(menus[i].id,false)}>私有</li> :
                                        <li onClick={()=>this.changePub(menus[i].id,true)}>公开</li>
                                }
                                <hr/>
                                <li onClick={()=>this.delMenu(menus[i].id)}>删除</li>
                                {
                                    i > 0 ? <li onClick={()=>this.moveUp(menus[i].id)}>上移</li> : ""
                                }
                                {
                                    i < menus.length-1 ? <li onClick={()=>this.moveDown(menus[i].id)}>下移</li> : ""
                                }
                                {/*<li>移动到...</li>*/}
                                <hr/>
                                <li onClick={()=>this.addChild(menus[i].id)}>添加子菜单</li>
                                <li onClick={()=>this.addSiblingBefore(menus[i].id)}>上方添加同级菜单</li>
                                <li onClick={()=>this.addSiblingAfter(menus[i].id)}>下方添加同级菜单</li>
                            </ul>
                        </i>
                    </dd>
                )
            }
        }
        return result;
    }
    getDL = (menus) => {
        const result = [];
        for(let i = 0; i < menus.length; i++) {
            result.push(
                <dl key={menus[i].id}>
                    {menus[i].child && menus[i].child.length>0 ? <i style={{float:"left",lineHeight:'37px',marginRight:"2px"}} className="fa fa-folder-open"></i> : ""}
                    <dt className={this.state.curr === menus[i].id ? "active" : ""}
                        onClick={()=>this.toggle(menus[i])}>
                        {
                            menus[i].edit ?
                                <Input value={menus[i].name} submit={(value)=>{this.confirmName(menus[i].id,value)}} /> :
                                menus[i].name
                        }
                        <i className={menus[i].pub ? "fa fa-unlock" : "fa fa-lock"}
                           onMouseOver={()=>this.showMenu(menus[i].id)}
                           onMouseOut={()=>this.hideMenu()}>
                            <ul className={this.state.actionMenu === menus[i].id ? "show" : "hide"}>
                                <li onClick={()=>this.reName(menus[i].id)}>重命名</li>
                                {
                                    menus[i].pub ?
                                        <li onClick={()=>this.changePub(menus[i].id,false)}>私有</li> :
                                        <li onClick={()=>this.changePub(menus[i].id,true)}>公开</li>
                                }
                                <hr/>
                                <li onClick={()=>this.delMenu(menus[i].id)}>删除</li>
                                {
                                    i > 0 ? <li onClick={()=>this.moveUp(menus[i].id)}>上移</li> : ""
                                }
                                {
                                    i < menus.length-1 ? <li onClick={()=>this.moveDown(menus[i].id)}>下移</li> : ""
                                }
                                {/*<li>移动到...</li>*/}
                                <hr/>
                                <li onClick={()=>this.addChild(menus[i].id)}>添加子菜单</li>
                                <li onClick={()=>this.addSiblingBefore(menus[i].id)}>上方添加同级菜单</li>
                                <li onClick={()=>this.addSiblingAfter(menus[i].id)}>下方添加同级菜单</li>
                            </ul>
                        </i>
                    </dt>
                    {menus[i].open ? this.getDD(menus[i].child) : ""}
                </dl>
            );
        }
        return result;
    }
    render() {

        return (
            <div className="menu">
                {
                    this.state.menu && this.state.menu.length>0 ?
                        this.getDD(this.state.menu) :
                        <div className="btn" onClick={this.addFirstMenu}>点击添加您的第一个菜单</div>
                }
            </div>
        );
  }
}

const mapStateToProps = (state) => {
    return {
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        login: (values) => {
            dispatch(actions.login(values));
        }
    }
};

export default connect(mapStateToProps,mapDispatchToProps)(Menu);

