import * as actionType from './actionType.js'
import * as config from '../config.js'
import * as Header from '../header'
export const loginSuccess = () => ({type: actionType.LOGIN_SUCCESS})

export const login = (values) => {
    return (dispatch, getState) => {
        const url = config.API_PATH+"/api/authenticate";
        return fetch(url, {
            method: 'POST',//方法大写
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            mode: "cors",
            body: JSON.stringify(values)
        }).then((response) => {
            //后台返回数据
            if (response.status !== 200) {
                throw new Error('Fail to get response with status ' + response.status);
            }

            response.json().then((responseJson) => {
                localStorage.setItem("token",responseJson.id_token)
                localStorage.setItem("user",values.username)
                dispatch({type:actionType.LOGIN_SUCCESS});
                dispatch(Header.actions.changeToLogin())
            }).catch((error) => {
                dispatch({type:actionType.LOGIN_FAILED});
            });
        }).catch((error) => {
            dispatch({type:actionType.LOGIN_FAILED});
        })
    };
}




