import * as actionType from './actionType.js';

export default (state = {
    errs: [],
    successed:false
}, action) => {
    switch (action.type) {
        case actionType.REGISTER_UPDATE_ERRS: {
            return {...state, errs:action.errs};
        }
        case actionType.REGISTER_SUCCESS: {
            return {...state, errs:[],successed:true}
        }
        default: {
            return state;
        }
    }
};

