import React, { Component } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Switch
} from 'react-router-dom';
import Container from './container';

class App extends Component {
  render() {
    return (
        <Router >
            <div className='app'>
                < Container.Header.view />
                <Switch>
                    <Route path="/register" component={Container.Register.view} />
                    <Route path="/login" component={Container.Login.view} />
                    <Route path="/" component={Container.Note.view} />
                </Switch>
            </div>
        </Router>
    );
  }
}

export default App;

