import React, { Component } from 'react';
import {connect} from "react-redux";
import Stackedit from 'stackedit-js';
import localForage from "localforage";
import md5 from "md5"
import * as actions from "../actions";

class Note extends Component {
    state = {
        text:""
    }

    openEdit = () => {
        const {match} = this.props;
        const {id} = match.params;
        const stackedit = new Stackedit();
        stackedit.on('fileChange', (file) => {
            this.setState({text:file.content.text,html:file.content.html})
        });
        stackedit.on('close', () => {
            localForage.setItem(id,{version:md5(this.state.text),text:this.state.text,html:this.state.html});
        });
        stackedit.openFile({
            content: {
                text: this.state.text
            }
        });

    }

    componentWillReceiveProps(nextProps) {
        const {id} = nextProps.match.params;
        localForage.getItem(id).then((note)=>{
            note = note || {};
            this.setState({text:note.text,html:note.html})
        })
    }

    render() {

        return (
            <div className="md">
                <span className="edit-note" onClick={()=>this.openEdit()}>编辑该文档</span>
                <div dangerouslySetInnerHTML={{__html: this.state.html}}/>

            </div>
        );

  }
}

const mapStateToProps = (state) => {
    return {
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        login: (values) => {
            dispatch(actions.login(values));
        }
    }
};

export default connect(mapStateToProps,mapDispatchToProps)(Note);

