import * as actionType from './actionType.js';

export default (state = {
    loginErr: false,
    registryErr: false,
    user: null
}, action) => {
    switch (action.type) {
        case actionType.HOME_SUCCESS: {
            return {...state, loginErr: false, registryErr: false};
        }
        default: {
            return state;
        }
    }
};

