import * as actionType from './actionType.js';

export default (state = {
    errs: [],
    successed:false
}, action) => {
    switch (action.type) {
        case actionType.LOGIN_SUCCESS: {
            return {...state,successed:true,errs:[]};
        }
        case actionType.LOGIN_FAILED: {
            return {...state,successed:false,errs:["登录名或密码错误"]};
        }
        default: {
            return state;
        }
    }
};

