import React, { Component } from 'react';
import {connect} from "react-redux";
import { Link } from "react-router-dom";

class Header extends Component {
    componentDidMount() {
    }

    getMenus = () => {
        const result = [];
        const token = localStorage.getItem("token")
        const user = localStorage.getItem("user")
        if(this.props.logined ||
            (token && user)) {
            result.push(<li key={1}><Link to="/user">{user}</Link></li>)
        } else {
            result.push(<li key={1}><Link to="/login">登录</Link></li>)
            result.push(<li key={2}><Link to="/register">注册</Link></li>)
        }
        return result;
    }
    render() {

        return (
            <div className="header">
                <span className="title"><i className="fa fa-pencil-square-o" aria-hidden="true"></i>LanBit</span>
                <ul className="topmenu">
                    {this.getMenus()}
                </ul>
            </div>
        );
  }
}

const mapStateToProps = (state) => {
    return {
        logined:state.header.logined
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
    }
};

export default connect(mapStateToProps,mapDispatchToProps)(Header);

