import React, { Component } from 'react';
import {connect} from "react-redux";
import { createForm } from 'rc-form';
import * as actions from "../actions";

class login extends Component {
    componentDidMount() {
    }
    submit = () => {
        this.props.form.validateFields((error, value) => {
            if(!error) {
                value.rememberMe = true;
                this.props.login(value)
            }
        });
    }
    componentWillReceiveProps(nextProps) {
        const {successed,history} = nextProps;
        if(successed) {
            history.push("/")
        }
    }
    render() {
        const {history} = this.props;
        const { getFieldProps, getFieldError} = this.props.form;
        const token = localStorage.getItem("token")
        const user = localStorage.getItem("user")
        if(token && user) {
            history.push("/")
        }

        return (
            <div className="login">
                <ul className="top-err">
                    {
                        this.props.errs.map(err=>(<li>{err}</li>))
                    }
                </ul>
                登录名
                <span className="err">
                    {getFieldError('username')?"必须录入登录名":"" }
                </span>
                <input {...getFieldProps('username', {
                    onChange(){}, // have to write original onChange here if you need
                    rules: [{required: true}],
                })}/>
                密码
                <span className="err">
                    {getFieldError('password')?"必须录入密码":"" }
                </span>
                <input type="password" {...getFieldProps('password', {
                    onChange(){}, // have to write original onChange here if you need
                    rules: [{required: true},{minLen:5}],
                })}/>

                <button className="btn btn-success" onClick={this.submit}>录入</button>
            </div>
        );
  }
}

const mapStateToProps = (state) => {
    return {
        errs:state.login.errs,
        successed:state.login.successed
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        login: (values) => {
            dispatch(actions.login(values));
        }
    }
};

export default createForm()(connect(mapStateToProps,mapDispatchToProps)(login));

